import React, {useState} from 'react'
import { Button } from '@mui/material';
import { connect } from 'react-redux';
import { Link, useNavigate, useParams} from 'react-router-dom';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import { reset_password_confirm } from '../../Actions/auth';
import '../../../Assets/CSS/Activate.css';


const Activate = () => {
const {uid, token} = useParams()
const [success, setSuccess] = useState(false)

  return (
    <div className='activate'>
        <h3>Verify Your Account</h3>
        {
          success ? 
          <div className='contain'>
            <CheckCircleOutlineIcon />
            <h4> Successful Thank You!</h4>
          </div>
          :

        <div className='contain'>  
            <Button variant='contained' color='secondary' onClick="" >Verify Account</Button>
        </div>

        }
   
    </div>
  )
}
const mapStateToProps = state => ({
  resetMsg : state.auth.resetMsg,
  isLoading: state.auth.isLoading,
})
export default connect(null, {reset_password_confirm}) (Activate);